import Button from "components/admin/modal/Button";
import Container from "components/admin/modal/Container";
import Content from "components/admin/modal/Content";
import Input from "components/admin/modal/Input";
import Title from "components/admin/modal/Title";
import { addCompany } from "controllers/admin";
import { useEffect, useRef, useState } from "react";

const Add = ({show, onClick}:IAdd) => {

  const ref = useRef<HTMLDivElement>(null);

  // 회사명
  const [name, setName] = useState<string>("");
  // 회사주소
  const [address, setAddress] = useState<string>("");
  // 회사 연락처
  const [telephone, setTelephone] = useState<string>("");
  // 카테고리
  const [category, setCategory] = useState<string>("");
  // 오픈시간
  const [open, setOpen] = useState<string[]>([]);
  // 태그
  const [tag, setTag] = useState<string>("");
  // 간판이미지
  const [signboard, setSignboad] = useState<any|any[]>(null);
  // 샘플이미지
  const [sample, setSample] = useState<any|any[]>(null);
  
  const onMouseDown = (e:any) => {
    if(!ref.current) return ;

    if(!e.path.includes(ref.current)) {
      onClick();
    }
  }

  const onSubmit = () => {
    addCompany(name, address, telephone, category, open, tag, signboard, sample);
    onClick();
  }

  useEffect(() => {
    if(show) {
      window.addEventListener('mousedown', onMouseDown, true);
    }
    return () => {
      window.removeEventListener("mousedown", onMouseDown, true);
    }
  },[show]);

  return (
    <Container>  
      <Content ref={ref}>
        <Title>업 체 추 가</Title>
        <Title>회사명</Title>
        <Input placeholder="회사명을 입력해주세요." 
              onChange={(e) => {setName(e.target.value)}}
        />
        <Title>업체주소</Title>
        <Input placeholder="회사주소를 입력해주세요." 
              onChange={(e) => {setAddress(e.target.value)}}
        />
        <Title>연락처</Title>
        <Input placeholder="연락처를 입력해주세요."
              onChange={(e) => {setTelephone(e.target.value)}}
        />
        <Title>카테고리</Title>
        <Input placeholder="카테고리를 입력해주세요." 
              onChange={(e) => setCategory(e.target.value)}
        />
        <Title>오픈시간</Title>
        <Input placeholder="평일 오픈 시간을 입력해주세요." 
              onChange={(e) => setOpen([...open, e.target.value])}
        />
        <Input placeholder="주말 오픈 시간 입력해주세요." 
              onChange={(e) => setOpen([...open, e.target.value])}
        />
        <Title>태그</Title>
        <Input placeholder="태그를 입력해주세요. ex) 철물, 합판, 철제" 
              onChange={(e) => setTag(e.target.value)}
        />
        <Title>업체 상호 이미지</Title>
        <Input type="file" accept="image/png, image/jpeg, image/jpg" 
              onChange={(e) => setSignboad(e.target.files)}
        />
        <Title>업체 샘플 이미지 3개</Title>
        <Input type="file" accept="image/png, image/jpeg, image/jpg" 
              onChange={(e) => setSample(e.target.files)}
        />
        <Button onClick={() => onSubmit()}>추&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;가</Button>
      </Content>
    </Container>
  )
}

interface IAdd {
  show: boolean;
  onClick: () => void;
}

interface IOpen {
  day: string;
  night: string;
}

export default Add;