import React, { useState } from 'react';

// components
import Title from 'components/filter/Title';
import Container from 'components/filter/Container';
import Filtering from 'components/filter/Filtering';
import Selected from 'components/filter/Selected';
import OpenFilter from 'components/filter/OpenFilter';
import Initialize from 'components/filter/Initialize';
import Search from 'components/filter/Search';
import LargeCategory from 'components/filter/LargeCategory';
import SmallCategory from 'components/filter/SmallCategory';

// data
import { filterTitle, filterItem } from 'modules/states/Filter';



const Filter = ({open, onClick, onSearch}:IFilter) => {
  const [list, setList] = useState<string[]>([]);
  const dupArr:string[] = [];

  const onListUp = (data:string) => {
      dupArr.push(data);
      dupArr.forEach((element) => {
        if (!list.includes(element)) {
          setList([...list, data]);
        }
      });
  }
  
  return (
  <>
    <Container>
      <Title>재료 검색</Title>
      <Selected>
        {
          list.map((data, index) => 
            <span key={index}>#{data} </span>
          )
        }
      </Selected>
      <Search onClick={() => onSearch(list)} list={list} />
      <Initialize onClick={() => setList([])}>
        검색초기화
      </Initialize>
      { open && 
        <Filtering> 
          {
            filterTitle.map(res => (
              <LargeCategory key={res.id}>{res.title}</LargeCategory>
            ))
          }
          {
            filterItem.map(res => (
              <SmallCategory key={res.id} onClick={() => onListUp(res.tag)}>{res.tag}</SmallCategory>
            ))
          }
        </Filtering>
      }
        <OpenFilter onClick={onClick} />
    </Container>
 </>
  )
}

interface IFilter {
  open : boolean;
  onClick : () => void;
  value ?: string[];
  onSearch : (name:string[]) => void;
}

export default Filter;