import Part from "components/admin/Part"
import Container from "components/admin/Container"
import Content from "components/admin/Content";
import { useContext, useState } from "react"
import Company from "./Company";
import Quest from "./Quest";
import User from "./User";
import { authContext } from "modules/contexts/auth";

// notfound
import NButton from "components/notfound/Button"
import NContainer from "components/notfound/Container"
import NTitle from "components/notfound/Title"

const Admin = () => {
  const [open, setOpen] = useState<IAdmin>({
    quest: true,
    user: false,
    company: false
  });
  const context = useContext(authContext);

  return (
    <>
    {
      context.state.user_id==='admin' ?
      <Container>
      <Part onClick={() => setOpen({quest:true, user:false, company: false})}>문의하기</Part>
      <Part onClick={() => setOpen({quest:false, user:true, company: false})}>회원관리</Part>
      <Part onClick={() => setOpen({quest:false, user:false, company: true})}>업체관리</Part>
      <Content>
        {
          open.quest && <Quest />
        }
        {
          open.user && <User />
        }
        {
          open.company && <Company />
        }
      </Content>
    </Container>
    : 
    <>
      <NContainer>
        <NTitle>접근권한이 없습니다.</NTitle>
        <NButton onClick={() => window.location.href='/'}>Home</NButton>
      </NContainer>
    </>
    }
   </>
  )
}

interface IAdmin {
  quest: boolean;
  user: boolean;
  company: boolean;
}

export default Admin;