import Fetch from 'controllers/fetch';

/**
@param { none }
@version 1
 */

export const getQuestion = () => {
  
  return Fetch("/api/v1/admin/question",'GET')
    .then(res => {
      return res.results;
    })
    .catch(err => {
      return Promise.reject(err);
    })
}

/**
@param { title, address, telephone, category, open, tag, signboard, sample } 회사명, 주소, 대표카테고리, 매장오픈시간, 취급물품, 간판, 샘플이미지
@version 1
 */
export const addCompany = (
  title: string,
  address: string,
  telephone: string,
  category: string,
  open:string[],
  tag:string,
  signboard: FormData,
  sample: FormData,
) => {
  
  const sendData = {
    title,
    address,
    telephone,
    category,
    open,
    tag
  };

  return Fetch("/api/v1/admin/company", "POST", sendData)
      .then(res => {
        return res.success;
      })
      .catch(err => {
        return Promise.reject(err);
      })
}

/**
@param { title } 회사명
@version 1
 */
export const deleteCompany = (title: string) => {
  const sendData = {
    title
  }
  return Fetch("/api/v1/admin/company","DELETE", sendData)
    .then(res => {
      return res.success;
    })
    .catch(err => {
      return Promise.reject(err);
    })
}

/**
 @param { id } 유저 아이디
 @version 1
 */
export const dropUser = (id:string) => {
  const sendData = {
    id
  }
  return Fetch("/api/v1/admin/user","DELETE", sendData)
    .then(res => {
      if(res.success) {
        return res.results;
      }
    })
    .catch(err => {
      return Promise.reject(err);
    })
}

/** 유저 정보 호출 
@param { none } 유저 가입에 필요한 정보
@version 1
*/
export const allUserInfo = () => {
  return Fetch('/api/v1/admin/user','GET')
      .then(async(res) => {
        if(res.success) {
          return res.results;
        }
      })
      .catch(err => {
        return Promise.reject(err);
      })
}
