import Fetch from 'controllers/fetch';
import reAuthorization from './reAuthorization';

/** 회원가입
@param {id, pwd, selected} 유저 가입에 필요한 정보
@version 1
*/
export const signUpUser = (user_id:string, pwd:string, selected:boolean) => {
  const name:string = "";
  const introduce:string = "";
  const interest:string = "";
  const sendData = {
    user_id,
    pwd,
    selected,
    name,
    interest,
    introduce
  }

  return Fetch('/api/v1/user/signup', 'POST', sendData)
      .then(res => {
        return res;
      })
      .catch(err => {
        return err;
      });
}

/** 유저 정보 호출 
@param { none } 유저 가입에 필요한 정보
@version 1
*/
export const userInfo = () => { 
  return Fetch('/api/v1/user/signin','GET')
      .then(async(res) => {
        if(res.success) {
          return res.results;
        } else if (res.statusCode === 401) {
          return await reAuthorization('/api/v1/user/singin', res.statusCode, userInfo);
        }
      })
      .catch(err => {
        return Promise.reject(err);
      })
}

/** 로그인
@param {id, pwd} 로그인에 필요한 정보
@version 1
*/
export const signInUser = (user_id:string, pwd:string) => {
  const sendData = {
    user_id,
    pwd
  };

  return Fetch('/api/v1/auth/signin','POST', sendData)
      .then(res => {
        if(res.success) {
          window.localStorage.setItem("lab-lg","true");
          return res.results;
        }
      })
      .catch(err => {
        return Promise.reject(err);
      }) 
}

/** 로그아웃
@param { none } 유저 가입에 필요한 정보
@version 1
*/
export const signOutUser = () => {
  return Fetch('/api/v1/user/logout','GET')
      .then(res => {
        window.localStorage.removeItem('lab-lg');
        return res;
      })
      .catch(err => {
        return Promise.reject(err);
      })
}

/**
 @param { user_id } 아이디
 @version 1
 */
export const idDuplicateCheck = (user_id:string) => {
  const sendData = {
    user_id
  };

  return Fetch('/api/v1/user/id', 'POST', sendData)
      .then(res => {
        return res.success;
      })
      .catch(err => {
        return Promise.reject(err);
      })
}

