/**
 * 토큰 갱신 API
 * @version 1
 */
export const renewalToken = () => {
	return Fetch("/api/v1/auth/access-token", "GET")
		.then(res => {
			if (res.success) {
				return Promise.resolve(res);
			} else {
				signOutUser();
				return Promise.reject(res);
			}
		});
}