import Button from "components/admin/modal/Button";
import Container from "components/admin/modal/Container";
import Content from "components/admin/modal/Content";
import Input from "components/admin/modal/Input";
import Title from "components/admin/modal/Title";
import { deleteCompany } from "controllers/admin";
import { useEffect, useRef, useState } from "react";

const Delete = ({show, onClick}:IDelete) => {

  const ref = useRef<HTMLDivElement>(null);

  // 회사명
  const [name, setName] = useState<string>("");

  const onMouseDown = (e:any) => {
    if(!ref.current) return ;

    if(!e.path.includes(ref.current)) {
      onClick();
    }
  }

  useEffect(() => {
    if(show) {
      window.addEventListener('mousedown', onMouseDown, true);
    }
    return () => {
      window.removeEventListener("mousedown", onMouseDown, true);
    }
  },[show]);

  const onSubmit = () => {
    deleteCompany(name);
    onClick();
  }

  return (
    <Container>
      <Content ref={ref}>
        <Title>업 체 삭 제</Title>
        <Title>회사명</Title>
        <Input placeholder="회사명을 입력해주세요." 
              onChange={(e) => setName(e.target.value)}
        />
        <Button onClick={() => onSubmit()}>삭&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;제</Button>
      </Content>
    </Container>
  )
}

interface IDelete {
  show: boolean;
  onClick: () => void;
}

export default Delete;