import Button from "components/auth/mypage/basic/Button";
import ButtonContainer from "components/auth/mypage/basic/ButtonContainer";
import Input from "components/auth/mypage/basic/Input";
import Part from "components/auth/mypage/basic/Part";
import TextArea from "components/auth/mypage/basic/TextArea";
import { updateUserInformation } from "controllers/auth";
import { authContext } from "modules/contexts/auth";
import { useContext, useEffect, useState } from "react";

const Basic = () => {
  const context = useContext(authContext);
  const [name, setName] = useState<string>(context.state.name);

  const [interest, setInterest] = useState<string>(context.state.interest);

  const [introduce, setIntroduce] = useState<string>(context.state.introduce);
  const onSubmit = async() => {
    await updateUserInformation(name, interest, introduce);
    window.location.href="/mypage";
  }

  useEffect(()=>{
    setName(context.state.name);
    setInterest(context.state.interest);
    setIntroduce(context.state.introduce);
  },[context.state]);

  return (
    <>
      <Part>
        이름
      </Part>
      <Input type="text" height={42} placeholder="직접 입력하기" onChange={(e) => setName(e.target.value)} value={name}/>
      <Part>
        관심분야
      </Part>
      <Input type="text" height={42} placeholder="직접 입력하기" onChange={(e) => setInterest(e.target.value)} value={interest}/>
      <Part>
        자기소개
      </Part>
      <TextArea placeholder="직접 입력하기" onChange={(e) => setIntroduce(e.target.value)} value={introduce}/>
      <ButtonContainer>
        <Button onClick={() => onSubmit()}>수정하기</Button>
      </ButtonContainer>
    </>
  )
}

export default Basic;