import Fetch from 'controllers/fetch';

export const getFilteredData = (data:string[]) => {
  const sendData = {
    category: data ? data : ""
  }
  return Fetch('/api/v1/filtering','POST', sendData)
              .then(res => {
                if(res.success){
                  return res.results;
                }
              })
              .catch(err => {
                return Promise.reject(err);
              })
}

export const getAllData = () => {
  return Fetch('/api/v1/material','GET')
            .then(res => {
              if(res.success){
                return res.results;
              }
            })
            .catch(err => {
              return Promise.reject(err);
            })
}