import styled from 'styled-components';

const Search = ({onClick, list}:ISearch) => {
 return <Img src="images/icons/Search.png" onClick={() => onClick(list)}/>
}

const Img = styled.img`
  position : absolute;
  right : 45px;
  cursor : pointer;

  @media (max-width : 414px) {
    right : 18px;
    width : 43px;
    height : 23px;
  }
`;

interface ISearch {
  list : string[]
  onClick : (list:string[]) => void;
}

export default Search;