import Button from "components/admin/Button";
import Container from "components/admin/company/Container";
import List from "components/admin/company/List";
import Name from "components/admin/company/Name";
import Order from "components/admin/company/Order";
import Ulist from "components/admin/company/Ulist";
import Title from "components/admin/Title";
import CompanyModal from "containers/common/CompanyModal";
import { getAllData } from "controllers/filter";
import { useCallback, useEffect, useState } from "react";
import Add from "./modal/Add";
import Delete from "./modal/Delete";

const Company = () => {

  const [open, setOpen] = useState<IOpen>({
    add: false,
    delete: false
  });
  const [show, setShow] = useState<boolean>(false);
  const [num, setNum] = useState<number>(0);
  const [result, setResult] = useState<any | any[]>([]);
  const [item, setItem] = useState<any | any[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  const onClick = () => {
    setShow(false);
  }

  const fetchMoreData = async() => {
    setIsLoading(true);
    setResult(result.concat(item.slice(0,20)));
    setItem(item.slice(20));
    setIsLoading(false);
  }

  const _infiniteScroll = useCallback(() => {

    let scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);

    let scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop);

    let clientHeight = document.documentElement.clientHeight;
    scrollHeight-=100;
    if(scrollTop + clientHeight >= scrollHeight && isLoading === false) {  
      fetchMoreData();
    }
  },[isLoading]);

  const getFetchData = async() => {
    let response;
    response = await getAllData();
    if(response) {
      setResult(response.slice(0,20));
      response = response.slice(20);
      setItem(response);
    }
    setIsLoading(false);
  }

  useEffect(() => {
    getFetchData();
  }, []);

  useEffect(() => {
    window.addEventListener('scroll', _infiniteScroll, true);
    return () => window.removeEventListener('scroll', _infiniteScroll, true);
  }, [_infiniteScroll]);

  return (
    <>
      <Title>업체관리</Title>
      <Button onClick={() => setOpen({...open, add:true})}>추가</Button>
      <Button onClick={() => setOpen({...open, delete:true})}>삭제</Button>
      {/* 추가 */}
      {
        open.add && <Add show={open.add} onClick={() => setOpen({...open, add:false})}/>
      }
      {/* 삭제 */}
      {
        open.delete && <Delete show={open.delete} onClick={() => setOpen({...open, delete:false})}/>
      }
      {/* 업체 data */}
      {
        isLoading ? <div>로딩 중 입니다:)</div>
                  : (
                    <Container>
                      <Ulist>
                        {
                          result.map((res:any, idx:number) => (
                            <List key={res._id} onClick={() => {
                              setShow(true);
                              setNum(idx);
                              }}>
                              <Order>{idx+1}</Order>
                              <Name>{res.title}</Name>
                            </List>
                          ))
                        }
                      </Ulist>
                    </Container>
                  )
      }
      {
        show && <CompanyModal result={result[num]} open={show} onClick={onClick} />
      }
    </>
  )
}

interface IOpen {
  add: boolean;
  delete: boolean;
}

export default Company;