import React, { useContext, useState } from "react";
import {Link} from 'react-router-dom';

// Containers
import Slidebar from "containers/common/Slidebar";
// Components
import Container from "components/common/header/Container";
import Menu from "components/common/header/Menu";
import Hamburger from "components/common/header/Hamburger";
import Logo from "components/common/header/Logo";
import Mypage from "components/common/header/Mypage";
import { authContext } from "modules/contexts/auth";
import AdminLink from "components/common/header/AdminLink";

const Header = () => {
  const context = useContext(authContext);

  const [open,setOpen] = useState(false);

  const onToggle = () => {
    setOpen(!open);
  }

  return (
    <>
      <Container>
        <Menu open={open} onClick={onToggle} >
          <Hamburger open={open} />
          <Hamburger open={open} />
          <Hamburger open={open} />
        </Menu>
        <Logo>
          <Link to="/">3355Lab</Link>
        </Logo>
        {
          context.state.user_id === 'admin' && <AdminLink onClick={() => window.location.href="/admin"}>관리자페이지</AdminLink>
        }
        {
          context.state.user_id
          ? <Mypage onClick={() => window.location.href="/mypage"} src="images/icons/Mypage.png" alt="마이페이지" />
          : <Mypage onClick={() => window.location.href="/signin"} src="images/icons/Mypage.png" alt="로그인" />
        }
        
      </Container>
      <Slidebar open={open} onClick={onToggle} /> 
    </>
  )
}

export default Header;